@import url('https://maxcdn.bootstrapcdn.com/font-awesome/4.2.0/css/font-awesome.min.css');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/6.1.2/css/all.min.css');
@font-face {
  font-family: "metro-bold";
  src: url("assets/fonts/Metropolis-Bold.otf");
}
@font-face {
  font-family: "metro-medium";
  src: url("assets/fonts/Metropolis-Medium.otf");
}
@font-face {
  font-family: "metro";
  src: url("assets/fonts/Metropolis-Regular.otf");
}
@font-face {
  font-family: "metro-thin";
  src: url("assets/fonts/Metropolis-Thin.otf");
}

* {
  box-sizing: border-box;
  letter-spacing: 1%;
}

body {
  margin: 0;
  font-family: metro;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

header {
  z-index: 9999;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;

  text-align: center;
  flex-direction: row;
  align-items: left;
  justify-content: left;
  height: 10vh;
  min-width: 100vw;
  min-height: 48px;
  max-height: 48px;
  pointer-events: all;
  background-color: #282c34;
}

header>#nav-left {
  display: flex;
  text-align: center;
  flex-direction: row;
  align-items: left;
  justify-content: left;
  width: 100%;
  min-height: 48px;
  max-height: 48px;
  pointer-events: all;
}

header>#nav-left>a {
  width: 100%;
  height: 48px;
  padding: 14px 0 0 0;

  font-family: metro;
  font-size: 18px;

  color: white;
  text-decoration: none;
  pointer-events: all;
  user-select: none;
}

header>#nav-left>a:hover {
  background-color: #3a3d44;
}

header>#nav-right {
  display: flex;
  justify-content: right;
  align-items: right;
  margin: 0 5vw 0 auto;
  padding-left: 8px;
}

header>#nav-right>button, .nav-user-modal>button {
  font-family: metro;
  font-size: 16px;
  user-select: none;
  text-transform: uppercase;

  min-width: 132px;
  width: 100%;
  max-width: 200px;
  height: calc(100% - 16px);
  margin: auto 10px auto 0;
  cursor: pointer;
  border: unset;
  border-radius: 6px;
  color: white;
  background-color: transparent;
}

header>#nav-right>button:hover {
  background-color: rgb(180, 107, 48) !important;
}

header>#nav-right>a {
  display: flex;
  text-decoration: none;
}

header>#nav-right>a>p {
  overflow-x: hidden;
  text-overflow: ellipsis;
  user-select: none;

  width: fit-content;
  max-width: 33vw;

  color: white;
  margin: auto auto;
  padding: 0 12px 0 8px;
}

header>#nav-right>a>i {
  color: white;
  font-size: 28px;
  padding: 0 12px 0 0;
  margin: auto auto;
}

.nav-right-user {
  cursor: pointer;
}

.nav-right-user:hover {
  background-color: #3a3d44;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'metro-medium';
}

p {
  color: white;
  font-family: metro;
  width: 100%;
}

img {
  user-select: none;
  pointer-events: none;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

#container {
  min-height: calc(100vh - 48px);
  max-height: calc(100vh - 48px);
  overflow-y: auto;
  overflow-x: hidden;
}

.no-select {
  pointer-events: none;
  -khtml-user-select: none;
  -o-user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}

.content {
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  padding: 0 0 128px 0;
  background-color: #282c34;
  font-size: calc(10px + 2vmin);
}

.page-content {
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: left;
  height: 100vh;
  color: rgb(40, 44, 52);
  background-color: #e5e7eb;
  font-size: calc(10px + 2vmin);
}

.error-page-content {
  display: flex;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  user-select: none;

  width: 100%;
  height: 100vh;
  margin-top: -28px;

  color: rgb(40, 44, 52);
  background-color: #e5e7eb;
  font-size: calc(10px + 2vmin);
}

.error-page-content>img {
  min-width: 128px;
  width: 10vw;
  max-width: 420px;
  height: auto;
}

::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background-color: rgb(212, 96, 0);
  outline: 1px solid rgb(77, 77, 77);
}

.rotate-45 {
  transform: rotate(45deg);
}

.rotate-90 {
  transform: rotate(90deg);
}

.rotate-180 {
  transform: rotate(180deg);
}

.rotate-270 {
  transform: rotate(90deg);
}

.rotate-360 {
  transform: rotate(360deg);
}

.grow {
  transition: all .2s ease-in-out;
}

.grow:hover {
  transform: scale(1.066);
}

.flex-left {
  display: flex;
  align-items: left;
  align-content: left;
  justify-content: left;
  min-width: 120px;
}

.flex-center {
  display: flex;
  justify-content: center;
  min-width: 120px;
}

.flex-between {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.flex-around {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.flex-evenly {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.flex-right {
  display: flex;
  justify-content: right;
  min-width: 120px;
}

.mobile-nav-menu-btn {
  color: white;
  cursor: pointer;
  font-size: 28px;
  min-width: 32px;

  border-radius: 4px;
  padding: 4px;
  margin: auto 28px auto 5vw;
}

.mobile-nav-menu-btn:hover {
  background-color: #3a3d44;
}

.mobile-nav-menu-bg {
  display: flex;
  flex-direction: column;
  position: fixed;
  user-select: none;

  top: 48px;
  left: 0;
  min-width: 100vw;
  min-height: calc(100vh - 48px);

  opacity: 95%;
  background-color: #282c34;
}

.mobile-nav-menu {
  display: flex;
  flex-direction: column;
  position: fixed;
  user-select: none;

  top: 48px;
  left: 0;
  min-width: 100vw;

  border-top: 1px solid rgba(255, 255, 255, 0.6);
}

.mobile-nav-menu>a {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  text-decoration: none;

  width: 100vw;
  min-height: 48px;
  height: 10vh;

  font-family: metro;
  font-size: 18px;

  color: white;
  background-color: #21242b;
  border-bottom: 1px solid rgba(255, 255, 255, 0.6);
}

.mobile-nav-menu>a:hover {
  background-color: #282c34;
}

.nav-user-modal-bg {
  display: flex;
  flex-direction: column;
  position: fixed;
  user-select: none;

  top: 48px;
  left: 0;
  min-width: 100vw;
  min-height: calc(100vh - 48px);

  opacity: 95%;
  background-color: #282c348f;
}

.nav-user-modal-head {
  display: flex;
  flex-direction: column;
  position: absolute;
  user-select: none;

  top: 0;
  left: 0;

  min-width: 100%;
  height: 28px;

  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  background-color: #282c34;
}

.nav-user-modal-close {
  cursor: pointer;
  color: white;
  margin: auto auto auto 16px;
}

.nav-user-modal {
  display: flex;
  flex-direction: column;
  position: fixed;
  user-select: none;
  z-index: 99;

  top: calc(50% + 24px);
  left: 50%;
  transform: translate(-50%, -50%);

  min-width: 300px;
  width: 90vw;
  max-width: 600px;

  min-height: 252px;
  height: calc(90vh - 48px);
  max-height: 552px;

  border-radius: 6px;
  background-color: #e5e7eb;
  box-shadow: 0px 0px 15px rgba(99, 99, 99, 0.75);
}

.nav-user-modal>h3 {
  margin: 10% auto 8px auto;
  font-family: metro;
}

.nav-user-modal>p {
  margin: 0 auto;
  text-align: center;
  max-width: 320px;
  color: black;
  font-size: 14px;
  font-family: metro;
}

#nav-user-modal-register-email {
  width: 90%;
  max-width: 320px;
  height: 36px;
  border-radius: 3px;
  padding: 0 6px;
  margin: 10% auto 0 auto;
  font-family: metro;
}

.nav-user-modal-foot {
  display: flex;
  flex-direction: row;
  position: absolute;
  user-select: none;
  text-align: center;

  bottom: 8px;
  left: 50%;

  font-size: 14px;
  font-family: metro-thin;
  transform: translate(-50%, 0);
}

.nav-user-modal>h1, .nav-user-modal>h2, .nav-user-modal>h3 {
  max-width: 80%;
  margin: 5% auto;
  padding: unset;
  font-family: metro;
}

.nav-user-modal>div {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
}

.nav-user-modal>div>button {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  min-width: 148px;
  padding: 16px;

  color: rgb(168, 114, 64);
  border-radius: 6px;
  border: 2px solid rgb(168, 114, 64);
  background-color: #f0f0f0;
}

.nav-user-modal>div>button:hover {
  color: #f0f0f0;
  background-color: rgb(168, 114, 64);
}

.nav-user-modal>input {
  z-index: 1;

  width: 70%;
  max-width: 500px;

  max-height: 64px;
  min-height: 64px;

  margin: 0 auto 16px auto;
  padding: 16px 16px 0 16px;
  border: 0 solid #fff;
  inset: none;

  border-radius: 6px;
  -webkit-border-radius: 6px;
}

.nav-user-modal>p {
  z-index: 2;
  user-select: none;
  pointer-events: none;
  text-align: left;

  width: 70%;
  max-width: 500px;

  height: 32px;
  margin: 8px 0 -36px 15%;
  padding: 0 8px 0 8px;

  color:rgb(85, 85, 85);
  border-radius: 6px;
}

.nav-user-modal>p>img {
  user-select: none;
  pointer-events: none;
  margin: 4px 12px -3px 8px;
}

.nav-user-modal>a {
  width: fit-content;
  margin: 7.5% auto 0 auto;
}

.nav-user-modal-login-error {
  width: 50%;
  max-width: 300px;

  margin: 0 auto 2.5% auto;
  padding: 1%;

  border-radius: 4px;
  font-family: metro;
  color: white;
  background-color: rgb(216, 31, 31);
}
