
.header {
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  user-select: none;
  width: 100%;
  height: 15vh;
}

.header>.logo {
  width: auto;
  height: 100%;
  pointer-events: none;
  user-select: none;
}

.home-background-video {
  display: flex;
  position: fixed;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  user-select: none;
  background-color: black;
  user-select: none;
}

.home-background-video>video {
  position: fixed;
  top: 0;
  opacity: 50%;
  filter: blur(15px);
  width: auto;
  height: 100vh;
  user-select: none;
}

@media all and (max-width: 640px){
  .home-background-video>video {
    left: -10vw;
  }
}

.home-page-content {
  display: flex;
  position: fixed;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  overflow-x: visible;
  overflow-y: visible;
  user-select: none;

  top: calc(50% + 32px);
  left: 50%;
  transform: translate(-50%, -50%);

  -webkit-transition-duration: 750ms;
  -webkit-transition-delay: 0ms;
  transition-duration: 750ms;
  transition-delay: 0ms;

  min-height: calc(70vh - 112px);
  height: calc(70vh - 112px);
  max-height: calc(70vh - 112px);
}

.home-page-highlight {
  display: flex;
  flex-direction: row;
  cursor: pointer;
  box-sizing: border-box !important;
  overflow: hidden;
  user-select: none;
  text-decoration: none;

  height: 15vh;
  max-height: 200px;
  width: 90vw;
  max-width: 600px;
  margin: auto auto;
  padding: 2vh 2vw;

  border-radius: 8px;
  border: 1px solid rgb(34, 34, 34);
  background-color: #242830d8;
}

.home-page-highlight:hover {
  box-shadow: 0px 0px 1vmax rgba(209, 113, 11, 1);
}

@media all and (min-width: 640px){
  .home-page-highlight {
    transition: all 0.2s ease-in-out;
  }
  .home-page-highlight:hover {
    transform: scale(1.066);
  }
}

.home-page-highlight>img {
  width: auto;
  height: 80%;
  padding: 8px;
  margin: auto 2vw auto 0;
}

.home-page-highlight>div>h3 {
  font-size: 16px;
  font-family: metro-thin;
  color: rgb(224, 224, 224);
  margin: unset;
  padding: unset;
  user-select: none;
}

.home-page-highlight>div>p {
  font-size: 14px;
  font-family: metro-thin;
  color: rgb(224, 224, 224);
  margin: 1vh 0;

  max-width: 95%;
  max-height: 8vh;

  padding: unset;
  overflow: visible;
  text-overflow: ellipsis;
  user-select: none;
}

@media all and (max-width: 720px){
  .home-page-highlight>div>p {
    font-size: 12px;
  }
}

@media all and (max-width: 640px){
  .home-page-highlight>div>p {
    font-size: 10px;
  }
}

@media all and (max-width: 320px){
  .home-page-highlight>div>p {
    font-size: 8px;
  }
}

.home-page-highlight-content {
  display: flex;
  flex-direction: column;
  position: fixed;

  top: calc(50% + 32px);
  left: 50%;
  transform: translate(300%, -50%);
  -webkit-transition-duration: 750ms;
  -webkit-transition-delay: 0ms;
  -webkit-animation-delay: 0ms;
  -webkit-animation-timing-function: ease;
  transition-duration: 750ms;
  transition-delay: 0ms;
  animation-delay: 0ms;
  animation-timing-function: ease;

  width: 90vw;
  max-width: 600px;

  height: calc(70vh - 112px);
  max-height: calc(70vh - 112px);

  padding: 8px 16px;
  background-color: rgba(210, 210, 255, 0.08);
  color: rgb(22, 150, 44)
}

.home-page-highlight-back {
  display: flex;
  cursor: pointer;
  text-align: center;
  align-items: center;
  justify-content: center;
  user-select: none;
  color: white;

  font-size: 18px;
  font-family: metro;

  width: fit-content;
  height: fit-content;
}

.home-page-highlight-back:hover {
  opacity: 50%;
}

.home-page-highlight-title {
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;

  width: fit-content;
  height: fit-content;

  margin: 0 0 0 auto;

  color: white;
  font-size: 18px;
  font-family: metro;
  text-transform: uppercase;
}

.highlight-single-icon {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  user-select: none;

  min-width: fit-content;
  width: 100%;

  min-height: fit-content;
  height: 50%;
  margin: 0 5%;

  color: white;
}

.highlight-single-icon:hover {
  background-color: rgba(210, 210, 255, 0.08);
}

.social-footer {
  display: flex;
  position: fixed;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  user-select: none;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, -50%);

  min-width: 300px;
  width: 25vw;
  max-width: 600px;

  height: 32px;
  max-height: 32px;

  padding: 24px;
  filter: drop-shadow(0 1px 5px rgba(210, 210, 255, 0.66));
}

.social-footer>a {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  user-select: none;
}

.social-footer>a>img {
  width: 24px;
  user-select: none;
}
