
.ol-document-selector-bg {
  z-index: 99;
  display: none;
  position: fixed;
  pointer-events: all;

  top: 48px;
  left: 0;
  width: 100vw;
  height: calc(100vh - 48px);

  background-color: transparent;
}

.ol-document-selector {
  display: flex;
  position: fixed;

  z-index: 99;
  top: 48px;
  left: 0;

  width: 100vw;
  height: 48px;

  color: white;
  border-top: 1px solid rgb(66, 66, 66);
  background-color: #282c34;
}

.ol-document-viewer {
  display: flex;
  flex-direction: column;
  position: fixed;
  overflow-y: auto;

  z-index: 90;
  top: calc(50vh + 48px);
  left: 50vw;
  transform: translate(-50%, -50%);

  min-width: 100vw;
  min-height: calc(100vh - 96px);
  height: calc(100vh - 96px);
  max-height: calc(100vh - 96px);

  padding: 36px 0;
  color: black;
  background-color: #e5e7eb;
}

.ol-document-viewer>h1 {
  width: 100%;
  text-align: center;
}

.ol-document-viewer>p {
  color: black;
  padding: unset;
  margin: unset;
}

.ol-document-download-container {
  display: flex;
  position: absolute;
  flex-direction: column;
  user-select: none;
  width: 100%;
  height: fit-content;
  margin: auto auto;
  padding: 5vh 0 128px 0;
  color: black;
  text-align: center;
}

.ol-document-download-container>div>p, .ol-document-download-container>p {
  margin: unset;
  padding: unset;
  font-size: 16px;
  color: black;
}

.ol-document-download-button {
  display: flex;
  position: fixed;
  align-items: center;
  justify-content: center;
  text-align: center;
  user-select: none;
  cursor: pointer;
  z-index: 99;

  top: 108px;
  left: 0;

  width: 128px;
  height: 32px;
  padding-left: 12px;

  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;

  color: white;
  background-color: #282c34;
}

@media all and (max-width: 640px){
  .ol-document-download-button {
    width: 42px;
    padding-left: unset;
  }
  .ol-document-download-button>p {
    display: none;
  }
}

.ol-document-selector>button {
  cursor: pointer;
  text-transform: capitalize;
  font-size: 16px;

  width: 100%;
  height: 80%;

  margin: auto 2.5%;

  color: white;
  border: unset;
  background-color: transparent;
}

.ol-document-selector>button:hover {
  background-color: #21242b;
}

.ol-document-selector>button>i {
  font-size: 14px;
  width: 24px;
  height: 16px;
}

.ol-document-selection {
  display: flex;
  flex-direction: column;
  position: fixed;

  z-index: 100;
  top: 96px;
  left: 0;

  min-width: 95vw;
  width: 95vw;
  min-height: 200px;
  height: fit-content;

  margin: 0 2.5%;
  padding: 8px;

  color: white;
  border: unset;
  background-color: #21242b;
}

.ol-document-selection>a {
  color: white;
  text-decoration: none;
}

.ol-document-option {
  display: flex;
  position: relative;
  text-transform: capitalize;
  cursor: pointer;
  z-index: 101;
  padding: 16px;
  border-bottom: 1px solid white;
}

.ol-document-option:hover {
  background-color: #17191d;
}

.part-header {
  width: 100%;
  text-align: center;
  font-family: metro-bold;
}

.ol-document {
  width: 95%;
  max-width: 800px;
  margin: 0 auto;
  padding: 0 0 25vh 0;
}

.ol-document>h1 {
  color: black;
  text-align: center;
  margin-bottom: 5vh;
}

.ol-document>h2, .ol-document>h3, .ol-document>h4 {
  color: black;
  text-align: left;
  margin: 2.5vh 0;
}

.ol-document>p {
  color: black;
  margin-bottom: 1vh;
}

.next-doc-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
  text-align: center;
  margin: 64px auto 0 auto;
  background-color: #c64;
  border-radius: 8px;
}

.next-doc-btn>a {
  font-size: 20px;
  color: white;
  padding: 16px;
  min-width: 200px;
  min-height: 32px;
}

.next-doc-btn>a>i {
  padding: 0 16px;
}
