
#background-gif {
  position: fixed;
  top: 0;
  left: 0;
  background-color: black;
}

#background-gif>img {
  width: 100vw;
  height: 100vh;
  opacity: 50%;
  filter: blur(15px);
}

#toggle-edit-profile {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  text-align: right;
  user-select: none;
  cursor: pointer;

  top: 16px;
  left: 0;

  width: 142px;
  height: 32px;
  padding: 0 12px 0 12px;

  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;

  color: white;
  background-color: #282c34;
}

#toggle-edit-profile>p {
  padding: 6px 0;
}

@media all and (max-width: 640px){
  #toggle-edit-profile {
    width: fit-content;
  }
  #toggle-edit-profile>p {
    display: none;
  }
}

#user-display-image {
  position: relative;

  top: 0;
  left: 50%;
  transform: translate(-50%, 0);

  width: 250px;
  height: 250px;

  border-radius: 3px;
  border: 1px solid rgba(0, 0, 0, 0.5);
}

#user-display-image>img {
  width: 100%;
  height: 100%;
}

#user-display-image-edit {
  position: absolute;
  text-align: center;
  cursor: pointer;

  left: 50%;
  bottom: 0;
  transform: translate(-50%, 0);

  width: 100%;
  height: fit-content;

  color: black;
  background-color: rgba(228, 228, 228, 0.5);
}

#user-display-image-edit:hover {
  text-decoration: underline;
}

#user-display-image-edit>i {
  padding: 16px;
}

#user-info-container {
  z-index: 0;
  position: absolute;
  overflow-x: hidden;
  overflow-y: hidden;

  top: 180px;
  left: 50%;
  transform: translate(-50%, 0);

  width: 100%;
  max-width: 1200px;
  min-height: 300px;
  height: calc(90vh - 228px);

  color: black;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  background-color: red;
}

#user-info-container>* {
  z-index: 1;
  position: relative;
}
